/* --------------------------HEADER-------------------------------------------- */

.modal-content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.3rem;
  outline: 0;
}

.headerShadow {
  border-radius: 0px 0px 10px 10px;
  box-shadow: 3px 3px 10px #ccc;
}

.imgs {
  padding-top: 0.5%;
  padding-bottom: 0.5%;
}

.pd {
  padding: 1%;
}

.svgicon {
  text-align: center;
  width: 25px;
  height: 20px;
  position: absolute;
  left: 10px;
  top: 20%;
  /* margin-top: -17px; */
  /* font-size: 1.5rem; */
  opacity: 0.2;
  transition: color 300ms;
}

