.bckLog {
  background-image: linear-gradient(
    135deg,
    #667eea 0%,
    #764ba2 100%
  ) !important;
  width: 100%;
  float: left;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  margin: 0;
  justify-content: center;
}

