.widget-chart {
  text-align: center;
  padding: 1rem;
  position: relative;
}

.widget-chart .widget-chart-content {
  position: relative;
  z-index: 5;
}

.icon-wrapper {
  width: 54px;
  height: 54px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

.icon-wrapper {
  display: flex;
  align-content: center;
  align-items: center;
}

.rounded {
  border-radius: 0.25rem !important;
}

.icon-wrapper .icon-wrapper-bg {
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 3;
  opacity: 0.2;
}

.bg-warning {
  background-color: #f7b924 !important;
}

.icon-wrapper i {
  margin: 0 auto;
  font-size: 1.7rem;
  position: relative;
  z-index: 5;
}

.widget-chart .widget-numbers {
  font-weight: 700;
  font-size: 2.5rem;
  display: block;
  line-height: 1;
  margin: 1rem auto;
}

.text-warning {
  color: #f7b924 !important;
}

.opacity-10 {
  opacity: 1 !important;
}
.fsize-1 {
  font-size: 0.95rem !important;
}
.widget-chart .widget-subheading {
  margin: -0.5rem 0 0;
  display: block;
  opacity: 0.6;
}

.widget-chart .widget-numbers + .widget-chart-flex,
.widget-chart .widget-numbers + .widget-description,
.widget-chart .widget-numbers + .widget-subheading {
  margin-top: -0.5rem;
}


.widget-chart .widget-description {
  margin: 1rem 0 0;
}

.opacity-8 {
  opacity: .8!important;
}

.text-danger {
  color: #d92550!important;
}

.pr-1, .px-1 {
  padding-right: 0.25rem!important;
}


